/* toast */
.toast {
  flex-basis: 350px;
  max-width: 350px;
  font-size: 0.875rem;
  background-color: rgba(255, 255, 255, 0.85);
  background-clip: padding-box;
  border: 1px solid rgba(0, 0, 0, 0.1);
  box-shadow: 0 0.25rem 0.75rem rgba(0, 0, 0, 0.1);
  opacity: 0;
  border-radius: 0.25rem;
}

.toast:not(:last-child) {
  margin-bottom: 0.75rem;
}

.toast.showing {
  opacity: 1;
}

.toast.show {
  display: block;
  opacity: 1;
}

.toast.hide {
  display: none;
}

.toast {
  position: fixed;
  top: 30px;
  right: 30px;
  left: auto;
  margin: auto;
  z-index: 1041;
  background-color: #fff;
  font-size: 16px;
  font-weight: 500;
  border: 0;
  min-width: 350px;
  flex-basis: 350px;
  pointer-events: none;
  transform: translateY(-80%);
  transition: 0.15s all linear;
  border-radius: 3px;
  box-shadow: 0px 8px 16px rgba(37, 42, 49, 0.24);
}

.toast.show,
.toast.showing {
  pointer-events: all;
  transform: translateY(0);
}

.toast-body {
  padding: 8px 16px;
  font-size: 14px;
  font-weight: 500;
  word-break: break-word;
  width: 100%;
}

