  width: 20px;
  height: 20px;
}
.toast .btn i {
  font-size: 24px;
  font-weight: 400;
  color: inherit;
}
@media (max-width: 767px) {
  .toast .flex-grow-1 {
    flex: 1 0 0;
  }

  .toast {
    top: auto;
    left: 10px;
    right: 10px;
    margin: auto;
    bottom: 40px;
    flex-basis: 100%;
    min-width: 0;
  }
}

